import { useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';

/**
 * Хук для автоматической проверки авторизации на защищённых страницах.
 * Если сессия истекла или токен невалиден — происходит logout и редирект на /login
 */
export function useAuthGuard() {
  const { user, loadUserProfile, logout, token } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    // Если нет токена — сразу на логин
    if (!token) {
      logout();
      navigate('/login');
      return;
    }
    // Проверяем сессию через API профиля
    loadUserProfile()
      .catch(() => {
        logout();
        navigate('/login');
      });
    // eslint-disable-next-line
  }, [token]);
}
